import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core'; 
import { CreateBillComponent } from './create-bill/create-bill.component';
import { DetailBillComponent } from './detail-bill/detail-bill.component'; 
import { ComponentModule } from './component/component.module';
import { SalesPaymentCardComponent } from './sales-payment-card.component';
import { BillType50Module } from '../components/bill-type/bill-type-50/bill-type-50.module';

const routes: Routes = [
  {
    path: '',
    component: SalesPaymentCardComponent
  },
  {
    path: 'create',
    component: CreateBillComponent
  },
  {
    path: 'detail',
    component: DetailBillComponent
  },
]

@NgModule({
  declarations: [
    SalesPaymentCardComponent,CreateBillComponent,DetailBillComponent
  ],
  imports: [
    CommonModule, 
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    BillType50Module,
    ComponentModule

  ]
})
export class SalesPaymentCardModule { }
